*{
    font-family: 'Open Sans';
}



html{
    font-size: 16px;

    @media (max-height: 720px) {
        font-size: calc(16px * 100vh/730px) !important;
    }
}

body{
    background-color: hsl(257, 40%, 49%);
    height: calc(100vh);
    padding: 4rem;

    @media (max-height: 720px) {
        padding: 3rem;
    }

    @media (max-width: 992px) {
        padding: 1.5rem;
    }

    &::before{
        background-image: url(./images/bg-desktop.svg);
        background-size: cover;
        background-repeat: no-repeat;

        @media screen and (max-width: 750px) {
            background-image: url(./images/bg-mobile.svg);
        }
    }
}

.logo-image{
    width: 11rem;   

    @media (max-height: 720px) {
        width: 10rem;
    }

    @media (max-width: 992px) {
        width: 8rem;
    }
}

.container{
    margin-top: 4.25rem;

    @media (max-width:992px){
        margin-top: 2rem;
        flex-direction: column;
    }

    @media (max-height: 720px) {
        margin-top: 2rem;
    }
}

.poppins-norm{
    font-family: 'poppins' !important;
    font-weight: 400 !important;
}

.poppins-bold{
    font-family: 'poppins' !important;
    font-weight: 600 !important;
}

.sans{
    font-family: 'Open Sans' !important;
    font-weight: 400 !important;
}

.site-desctiption{
    @media (max-width:992px){
        margin-top: 3rem;
    }
}

.site-desctiption__title{
    line-height: 1.5;

    font-size: 1.75rem;

    @media (min-width: 992px) {
        font-size: clamp(1rem, 3vw, 2.25rem);  
    }
}

.site-desctiption__text{
    line-height: 1.7;
    color: hsl(300, 69%, 90%) !important;
}

.cta-btn{
    color: hsl(257, 40%, 49%) !important;
    background-color: white !important;

    &:hover{
        background-color: hsl(300, 75%, 75%) !important;
        color: white !important;
    }
}

.site-image{
    img{
        min-width: auto;
        min-height: 20rem;
        @media (min-width: 992px) {
            min-width: 42rem;   
            min-height: 30rem;
        }
    }
}

.social-icon{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    font-size: 1.25rem;

    &:hover{
        border-color: hsl(300, 75%, 61%);
        color: hsl(300, 75%, 61%);
    }
}